<template>
  <el-card class="box-card">
    <el-button type="primary" @click="$router.push('/admin/main/restaurant/add-restaurant')">Add Restaurant</el-button>
    <el-button type="primary" @click="$router.push('/admin/main/restaurant/start-funding')">Start Funding</el-button>
    <el-button type="primary" @click="$router.push('/admin/main/restaurant/purchase-ingredients')">Purchase Ingredients</el-button>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="longName">
        <el-input placeholder="longName" v-model="param.longName"></el-input>
      </el-form-item>
      <el-form-item label="rating">
        <el-input placeholder="rating" v-model="param.rating"></el-input>
      </el-form-item>
      <el-form-item label="spent">
        <el-input placeholder="spent" v-model="param.spent"></el-input>
      </el-form-item>
      <el-form-item label="location">
        <el-input placeholder="location" v-model="param.location"></el-input>
      </el-form-item>
      <el-form-item label="fundedBy">
        <el-input placeholder="fundedBy" v-model="param.fundedBy"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="longName"
          prop="longName"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="rating"
          prop="rating"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="spent"
          prop="spent"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="location"
          prop="location"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="fundedBy"
          prop="fundedBy"
          sortable
      >
        <template v-slot="scope">
          {{scope.row.fundedBy ? scope.row.fundedBy : 'n/a'}}
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="changePage"
    >
    </el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        longName: '',
        rating: '',
        spent: '',
        location: '',
        fundedBy: '',
      },
      tableList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0
    }
  },
  mounted() {
    this.getTableList(this.pageInfo)
  },
  methods: {
    getTableList() {
      this.$axios.post('/restaurants/findAll', Object.assign(this.param, this.pageInfo)).then(res => {
        this.tableList = res.data.rows;
        this.total = res.data.count;
      })
    },
    searchForm() {
      this.pageInfo.pageNum = 1;
      this.getTableList();
    },
    changePage(e) {
      this.pageInfo.pageNum = e
      this.getTableList()
    }
  },
}
</script>

<style scoped lang="scss">
.demo-form-inline, .el-pagination {
  margin-top: 10px;
}
</style>

